@border-color: rgba(189, 189, 189, 0.33);
@border-style: solid;
@border-width: 1px;
@zero: 0;
@comment-icon-font-size: 20px;

#heading-byline-margin-bottom() {
  margin-bottom: 5px;
}
#heading-byline-margin-top-zero() {
  margin-top: 0;
}
#flex-grow(@value:0) {
  flex-grow: @value;
}
#display-flex() {
  display:flex;
}
#flex-direction-row-reverse() {
  flex-direction: row-reverse;
}

.cbb-blog2-instance {
  margin: 20px @zero;
  .cbb-blog2 {
      padding: @zero;
      margin: @zero -15px 15px;
      list-style:none !important;
      > li:after, > li:before {
      display:none !important;
    }
    .isotope-blog2-item {
      padding: @zero;
      max-width: 100%;
      float: left;
      margin-left: -1px;
      .cbb-blog2-item {
        padding: @zero;
        border: @border-width @border-style @border-color;
        *{
          transition: background-color 0.3s, border-color 0.3s, box-shadow 0.3s;
        }
        a.cbb-blog2-img {
          text-align: center;
          display: block;
          img {
            object-fit: cover;
            width: 100%;
          }
        }
        .cbb-blog2-item-inner {
          width: 100%;
          padding: 15px 10px;
          h3.cbb-blog2-title {
            margin-bottom:0;
            #heading-byline-margin-top-zero;
            a {
              text-decoration: none;
            }
          }
          .byline {
            #heading-byline-margin-bottom;
          }
          .cbb-blog2-layout-links.cbb-blog2-btn-dots {
             justify-content: flex-end;
          }
          .cbb-blog2-layout-links {
            margin-top: 0;
            #display-flex;
            #flex-direction-row-reverse;
            justify-content: space-between;
            flex-wrap: wrap;
            .cbb-blog2-layout-btn {
              #flex-grow;
              .cbb-blog2-btn {
                width: 100%;
                padding: 5px 10px;
              }
            }
            .cbb-blog2-comment {
              #display-flex;
              align-items: center;
              #flex-grow(10);
              margin-left: 5px;
              margin-right: 5px;
              justify-content: flex-start;
              .cbb-blog2-comment-icon {
                font-size: @comment-icon-font-size;
                margin-bottom: 3px;
                margin-right: 5px;
              }
              .cbb-blog2-comment-number {
                margin-bottom: 0;
              }
            }
            .cbb-blog2-layout-category-btn {
              #flex-grow(1);
              .cbb-blog2-category-btn {
                padding: 5px 10px;
              }
            }
          }
          .cbb-blog2-layout-links.btn-left {
            justify-content: flex-end;
          }
        }
      }
    }
  }
  ul.pagination {
  margin: 0 0 0 13px;
  }
  .cbb-blog2-layout-list {
    .isotope-blog2-item {
      width: 100%;
      .cbb-blog2-item {
        @media(max-width: 767px) {
          display: block;
        }
        #display-flex;
        padding: 15px 0 15px 15px;
        .cbb-blog2-img {
          padding-right: 20px;
        }
        .cbb-blog2-item-inner {
          @media(max-width: 767px) {
            padding: 15px 0;
          }
          width: 100%;
          #display-flex;
          padding: @zero;
          flex-direction: column;
          .cbb-blog2-title,.byline {
            #flex-grow(1);
          }
          .cbb-blog2-text {
            #flex-grow(10);
          }
          .cbb-blog2-layout-links {
            .cbb-blog2-layout-btn {
              #flex-grow;
              .cbb-blog2-btn {
                border-bottom-right-radius: 0;
                border-top-right-radius: 0;
              }
            }
            .cbb-blog2-comment {
              justify-content: flex-end;
              margin-right: 5%;
              .cbb-blog2-comment-number {
                #flex-grow;
              }
              .cbb-blog2-comment-icon {
                #flex-grow;
              }
            }
            .cbb-blog2-layout-category-btn {
              #flex-grow;
            }
          }
        }
      }
    }
  }
  .cbb-blog2-layout-image_grid,.cbb-blog2-layout-image_grid_masonry {
    .isotope-blog2-item {
      .image_grid {
        .cbb-blog2-item-inner {
          width: 100%; 
        }
      }
    }
  }
  .cbb-blog2-layout-photo_shell {
    .isotope-blog2-item {
      .cbb-blog2-item {
        .cbb-blog2-item-inner {
          padding: 10px 7px;
          .cbb-blog2-title {
            margin-bottom: 10px !important;
          }
        }
      }
    }
  }

}
#sidebar .cbb-blog2-img img {
  object-fit: cover;
  height: auto;
}

.col-md-4 &, .col-md-3 { 
  .cbb-blog2-layout-list {
    .cbb-blog2-item {
      .cbb-blog2-img {
        img {
          width: 160px !important;
          height: auto;
        }
      }
      .cbb-blog2-item-inner {
        .cbb-blog2-title {
          font-size: 16px;
        }
        .byline, .cbb-blog2-text, .cbb-blog2-layout-links {
          font-size: 12px;
        }
        .cbb-blog2-layout-links {
          & * {
            margin-top: 3px;
          }
          .cbb-blog2-comment {
          }
        }
      }
    }
  }
}
@media (min-width: 768px) {
  .col-md-2 { 
    .cbb-blog2 {
      .cbb-blog2-item {
        .cbb-blog2-item-inner {
          .cbb-blog2-title {

          }
          .cbb-blog2-layout-links, .cbb-blog2-comment, .cbb-blog2-layout-category-btn {
            justify-content: center !important;
          }
        }
      }
    }
  }
}